import { useRef, useState } from 'react'
import { useSpring, animated, interpolate } from 'react-spring'
import theme from '../../theme'

const StandardInput = () => {
  const [isSpring, setIsSpring] = useState(false)
  const { spring } = useSpring({
    spring: isSpring ? 100 : 0,
    from: { spring: 0 }
  })
  return (
    <>
      <animated.input
        type="text"
        style={{
          border: `${spring.interpolate(spring => spring * 0.5)}px solid
        ${theme.palette.shade[30]}`
        }}
        onFocus={() => setIsSpring(true)}
        onFocus={() => setIsSpring(false)}
      />
      <style jsx>{`
        input {
          border-radius: 4px;
          padding: ${theme.spacing}px ${theme.spacing * 2}px;
          outline: none;
        }
      `}</style>
    </>
  )
}

export default StandardInput
